}

.navbar .nav-link.active {
  color: #E0234F !important;
  font-weight: 600;
  padding-left: 0px !important;
}

.navbar a.nav-link svg circle {
  transition: all .6s;
}

.navbar a.nav-link:hover svg circle {
  /* fill: #a2a2a2; */
  fill: #dddddd;
  transition: all .6s;

}

.navbar a.nav-link:hover g path {
  fill: #ff5e84;
  box-shadow: 0px -2px 8px white;
}

.nav-second-section {
