/* Call Us */


.Callus {
  position: fixed;
  bottom: 0%;
  right: 0%;
  transform: translate(-50%, -50%);
  z-index: +9999;
}

.Callus svg {
  width: 29px;
}

.Callus ul {
  list-style: none;
}

.Callus ul li {
  width: 50px;
  height: 50px;
  position: relative;
  background: linear-gradient(231deg, #91db63, #4de57c);
  margin: 10px 0;
  cursor: pointer;
  border-radius: 50%;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
  display: flex;
  justify-content: center;
  align-items: center;
}

.Callus ul li .fa {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 20px;
  color: #fff;
}

/* .Callus ul li.facebook{
  background: #3b5998;
} */


.Callus ul li.facebook div.slider {
  background: #e5e5e5;
}



.Callus .slider {
  content: "";
  position: absolute;
  top: 12%;
  right: 51px;
  display: flex;
  justify-content: center;
  height: 37px;
  width: 0px;
  background: #eebb5c;
  border-radius: 20px;
  transition: all 0.5s 0.3s ease;
  align-items: center;
}

.Callus .slider p {
  text-transform: uppercase;
  font-size: 16px;
  font-weight: 600;
  color: #6d6d6d;
  text-align: center;
  /* line-height: 50px; */
  opacity: 0;
  transition: all 0.6s ease;
  margin-bottom: 0px;
}

.Callus ul li:hover .slider {
  width: 180px;
  transition: all 0.5s ease;
}

.Callus ul li:hover .slider p {
  opacity: 1;
  transition: all 1s 0.2s ease;
}


